.about{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap:20px;
    margin:80px 170px;
}
.about-title{
    position: relative;
}
.about-title h1{
    padding: 0px 30px;
    font-size: 50px;
    font-weight: 600;
}
.about-sections{
    display:flex;
    gap:20px;
}
.about-right{
    display: flex;
    flex-direction: column;
    gap:10px;
}
.about-para{
    display:flex;
    flex-direction: column;
    font-size: 20px;
    font-weight: 500;
    gap:5px
}
.about-skills{
    display: flex;
    flex-direction: column;
    font-size: 15px;
    gap:14px;

}
.about-skill{
    display: flex;
    align-items: center;
    gap:30px;
    transition:0.3s;
}
.about-skill p{
    min-width:89px;
    font-size: 15px;
    font-weight: 500;
    
}
.about-skill hr{
    outline: none;
    width: 20%;
    height: 4px;
    border-radius: 50px;
    background:linear-gradient(to right,#df8909,#bf15ff);
}
.about-achievements{
    display: flex;
    width: 100%;
    justify-content: space-between;
     margin-bottom:80px;
     gap:10px;
}
.about-achievement{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:2px;
    transition: 0.5s;
}
.about-achievement h1{
    font-size: 60px;
    font-weight: 700;
    background: linear-gradient(270deg,#df8909 50.41%,#bf15ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.about-achievement p{
    font-size: 15px;
    font-weight: 500;
}
.about-skill:hover{
    transform: scale(1.05);
    transition: 0.3s;

}
.about-achievement:hover{
    transform: scale(0.98);
    transition: 0.5s;
}


/* Responsive styles for mobile */
@media (max-width: 768px) {
    .about {
        margin: 40px 20px; /* Reduce margins for smaller screens */
        gap: 15px; /* Adjust gap */
    }

    .about-title h1 {
        font-size: 35px; /* Smaller font size for headings */
        padding: 0 15px;
    }

    .about-sections {
        flex-direction: column; /* Stack sections vertically */
        gap: 15px;
    }

    .about-right {
        gap: 8px; /* Reduce gap between items */
    }

    .about-para {
        font-size: 18px; /* Slightly smaller font size */
    }

    .about-skills {
        gap: 10px; /* Reduce spacing between skills */
    }

    .about-skill {
        gap: 20px; /* Narrower gaps */
    }

    .about-skill p {
        min-width: auto; /* Allow flexibility for text */
        font-size: 14px; /* Smaller font size */
    }

    .about-skill hr {
        width: 30%; /* Adjust width for mobile */
        height: 3px; /* Slightly thinner */
    }

    .about-achievements {
        flex-direction: column; /* Stack achievements vertically */
        align-items: center; /* Center align for better appearance */
        gap: 15px; /* Adjust spacing */
    }

    .about-achievement h1 {
        font-size: 40px; /* Smaller font size for numbers */
    }

    .about-achievement p {
        font-size: 14px; /* Smaller font size for description */
    }
}
